import Vue from 'vue'
import VueRouter from 'vue-router'
import Members from '../components/contents/Members.vue'
import Search from '../components/contents/Search.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/vue-curriculum/demo/members',
    name: 'Members',
    component: Members
  },
  {
    path: '/vue-curriculum/demo/search',
    name: 'Search',
    component: Search
  },
  { path: '*', redirect: '/vue-curriculum/demo/members' },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
